export default class BbwzPathHelper{
    public static readonly basePath = "";
    public static readonly gameAudioPath = BbwzPathHelper.basePath + "sound/";
    public static readonly gamePrefabsPath = BbwzPathHelper.basePath + "prefabs/";
    public static readonly gameTexturePath = BbwzPathHelper.basePath + "texture/";
    public static readonly gameEffectPath = BbwzPathHelper.basePath + "effect/";
    public static readonly gameSoundPath = BbwzPathHelper.basePath + "sound/";
    
    //图集路径
    public static atlasPath(atlas){
        return BbwzPathHelper.gameTexturePath + atlas;
    }
}